import React from "react";
import * as MSPs from "../Data/MSPs";
import * as MSP from "../DTOs/MSP";
import "../App.css";
import BootstrapTable from "react-bootstrap-table-next";
import filterFactory, {
  numberFilter,
  textFilter
} from "react-bootstrap-table2-filter";
import * as Candidates from "../Data/Candidates";
import { ConstituencyToRegionMap } from "../Data/ConstituencyToRegionMap";
import Tories from "../Media/Tories.jpg";
import Greens from "../Media/Greens.png";
import Labour from "../Media/Labour.png";
import LibDems from "../Media/LibDems.png";
import SNP from "../Media/SNP.svg";
import ReformUK from "../Media/ReformUK.png";
import { default as Select } from "react-select";
import { ActionMeta, ValueType } from "react-select";

interface IConstituencyMap {
  constituency: MSP.Constituency;
  msps: IMSPSupport[];
}

interface IMSPSupport {
  name: JSX.Element;
  party: JSX.Element;
  support: MSP.Support;
  constituency?: MSP.Constituency;
  statement: string;
  regionRank?: number;
}

interface IConstituencies {
  value: MSP.Constituency;
  label: string;
}

interface ITablePageState {
  SectionSelected: SelectedSection;
  Constituency?: IConstituencyMap;
}

interface CandidateInfo {
  name: JSX.Element;
  party: JSX.Element;
  haveBeenContacted: boolean | undefined;
  support: MSP.Support;
  region: string;
  regionRank: string | number | undefined;
  constituency: string | null;
  statement: string;
}

interface IncumbentInfo {
  name: JSX.Element;
  party: JSX.Element | undefined;
  haveBeenContacted: boolean | undefined;
  support: MSP.Support;
  region: string;
  constituency: string | null;
  statement: string;
}

enum SelectedSection {
  Neither,
  AllCandidates,
  Incumbents
}

class TablePage extends React.Component<{}, ITablePageState> {
  public constructor(props: {}) {
    super(props);

    this.state = {
      SectionSelected: SelectedSection.Neither,
      Constituency: undefined
    };
  }

  public render(): JSX.Element {
    const constituencies = new Array<IConstituencies>();
    let i = 1;
    while (i < 72) {
      constituencies.push({
        value: i,
        label: this.CamelCaseToSentence(MSP.Constituency[i])
      });
      i++;
    }

    const data = MSPs.MSPs.MSPsCollection.filter(m => !m.StandingDown);
    let dataMap = data.map(d => ({
      name: this.FormatName(d.Name, true, d.Support),
      party: this.FormatParty(d.Party),
      haveBeenContacted: d.HaveBeenContacted,
      support: d.Support,
      region: this.FormatRegion(d.Constituency, d.Region),
      constituency: d.Constituency
        ? this.CamelCaseToSentence(MSP.Constituency[d.Constituency])
        : null,
      statement: d.Statement,
      regionRank: d.Constituency == null ? "N/a" : d.RegionRank
    }));

    this.SortIncumbents(dataMap);

    const data2 = MSPs.MSPs.MSPsCollection.filter(m => m.StandingDown);
    const dataMap2 = data2.map(d => ({
      name: this.FormatName(d.Name, true, d.Support),
      party: this.FormatParty(d.Party),
      region: this.FormatRegion(d.Constituency, d.Region),
      constituency: d.Constituency
        ? this.CamelCaseToSentence(MSP.Constituency[d.Constituency])
        : null
    }));

    const data3 = Candidates.MSPs.MSPsCollection.filter(m => !m.StandingDown);
    const dataMap3 = data3
      .map(d => ({
        name: this.FormatName(d.Name, false, d.Support),
        party: this.FormatParty(d.Party),
        haveBeenContacted: d.HaveBeenContacted,
        support: d.Support,
        region: this.FormatRegion(d.Constituency, d.Region),
        constituency: d.Constituency
          ? this.CamelCaseToSentence(MSP.Constituency[d.Constituency])
          : null,
        statement: d.Statement,
        regionRank: d.Constituency ? "N/a" : d.RegionRank
      }))
      .concat(dataMap);

    const dataMapCandidates = new Array<CandidateInfo>();

    dataMap3.forEach(d => {
      const candidate = dataMapCandidates.find(c => c.name.key === d.name.key);
      if (candidate) {
        candidate.name = <b>{candidate.name}</b>;
        candidate.constituency = candidate.constituency
          ? candidate.constituency
          : d.constituency;
        candidate.regionRank = candidate.regionRank
          ? candidate.regionRank
          : d.regionRank;
      } else {
        dataMapCandidates.push(d);
      }
    });

    this.SortCandidates(dataMapCandidates);

    const columns = [
      {
        dataField: "name",
        text: "Name",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      {
        dataField: "party",
        text: "Party",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      {
        dataField: "haveBeenContacted",
        text: "Have Been Contacted",
        sort: true,
        formatter: (value: boolean): JSX.Element => {
          return this.FormatContacted(value);
        }
      },
      {
        dataField: "support",
        text: "Support",
        sort: true,
        formatter: (value: MSP.Support): JSX.Element => {
          return this.FormatSupport(value);
        }
      },
      {
        dataField: "region",
        text: "Region",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      {
        dataField: "constituency",
        text: "Constituency",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      { dataField: "statement", text: "Statement" }
    ];

    const columns2 = [
      {
        dataField: "name",
        text: "Name"
      },
      {
        dataField: "party",
        text: "Party"
      },
      {
        dataField: "region",
        text: "Region",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      {
        dataField: "constituency",
        text: "Constituency",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      }
    ];

    const columns3 = [
      {
        dataField: "name",
        text: "Name"
      },
      {
        dataField: "party",
        text: "Party",
        headerStyle: () => {
          return { width: "250px" };
        }
      },
      {
        dataField: "support",
        text: "Support",
        sort: true,
        formatter: (value: MSP.Support): JSX.Element => {
          return this.FormatSupport(value);
        }
      },
      {
        dataField: "region",
        text: "Region",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" })
      },
      {
        dataField: "regionRank",
        text: "Region Rank",
        sort: true,
        filter: numberFilter({ placeholder: "Enter number" })
      },
      {
        dataField: "constituency",
        text: "Constituency",
        sort: true,
        filter: textFilter({ placeholder: "Enter search" }),
        headerStyle: () => {
          return { width: "300px" };
        }
      },
      { dataField: "statement", text: "Statement" }
    ];

    const showHeader = this.state.Constituency ? "block" : "none";

    return (
      <div className="App main-table">
        <p>
          <b>
            Are you an MSP or candidate who wants to make sure your views are
            represented here?
          </b>
        </p>
        <p>
          Read our <a href={"/request"}>request</a> and{" "}
          <a href="mailto:endconversiontherapyscotland@gmail.com?subject=Candidate Reply">
            email us
          </a>{" "}
          and we'll make sure your response is listed here.
        </p>
        <p>
          Our email address is{" "}
          <a href="mailto:endconversiontherapyscotland@gmail.com?subject=Candidate Reply">
            endconversiontherapyscotland@gmail.com
          </a>
          .
        </p>
        <p>
          If we know an email has been sent to an incumbent MSP by a constituent
          they will have "Have Been Contacted" confirmed below.
        </p>
        <div className="candidate-table">
          <h3>Select your Constituency</h3>
          <Select
            name={"Select your constituency"}
            options={constituencies}
            onChange={this.ConstituencyMap}
          />
        </div>
        <div className="constituency-list">
          <div className="headers" style={{ display: showHeader }}>
            <div className="cand-name">Name</div>
            <div className="cand-party">Party</div>
            <div className="cand-con">Type</div>
            <div className="cand-rr">List Rank</div>
            <div className="cand-support">Support</div>
            <div className="cand-stat">Statement</div>
          </div>
          <ul>
            {this.state?.Constituency?.msps.map(m => (
              <li>
                <ul className="candidates">
                  <li className="cand-name">{m.name}</li>
                  <li className="cand-party">{m.party}</li>
                  <li className="cand-con">
                    {m.constituency === this.state.Constituency?.constituency
                      ? "Constituency Candidate"
                      : "List Candidate"}
                  </li>
                  <li className="cand-rr">{m.regionRank}</li>
                  <li className="cand-support">
                    {this.FormatSupport(m.support)}
                  </li>
                  <li className="cand-stat">{m.statement}</li>
                </ul>
              </li>
            ))}
          </ul>
        </div>
        <div className="select-buttons">
          <button
            disabled={
              this.state.SectionSelected === SelectedSection.AllCandidates
            }
            onClick={this.SelectAllCandidates}
          >
            All Candidates
          </button>
          <button
            disabled={this.state.SectionSelected === SelectedSection.Incumbents}
            onClick={this.SelectIncumbents}
          >
            Incumbent MSPs
          </button>
        </div>
        {this.state.SectionSelected === SelectedSection.Incumbents && (
          <>
            <h1>Incumbent MSPs</h1>
            <BootstrapTable
              keyField="name"
              data={dataMap}
              columns={columns}
              bordered={true}
              filter={filterFactory()}
            />

            <h1 style={{ maxWidth: "1000px" }}>Incumbent MSPs Standing Down</h1>
            <BootstrapTable
              keyField="name"
              data={dataMap2}
              columns={columns2}
              bordered={true}
              filter={filterFactory()}
            />
          </>
        )}
        {this.state.SectionSelected === SelectedSection.AllCandidates && (
          <>
            <h1>Candidates</h1>
            <h3>Incumbent MSPs are in bold</h3>
            <p>
              <b>Note: </b>This is not a final list of candidates as selections
              are still ongoing in several parties. <br /> If you know of
              candidate details that have been announced but are not in our
              table please let us know at:{" "}
              <a href="mailto:endconversiontherapyscotland@gmail.com?subject=Candidate Reply">
                endconversiontherapyscotland@gmail.com
              </a>
            </p>
            <BootstrapTable
              keyField="name"
              data={dataMapCandidates}
              columns={columns3}
              bordered={true}
              filter={filterFactory()}
            />
          </>
        )}
      </div>
    );
  }

  private FormatContacted(value: boolean): JSX.Element {
    switch (value) {
      case true:
        return <b style={{ margin: 0, color: "#06a800" }}>{"Yes"}</b>;
      case false:
        return <p style={{ margin: 0, color: "#333333" }}>{"No"}</p>;
    }
  }

  private FormatSupport(value: MSP.Support): JSX.Element {
    switch (value) {
      case MSP.Support.NoReply:
        return (
          <p style={{ margin: 0, color: "#555555" }}>
            {this.CamelCaseToSentence(MSP.Support[value])}
          </p>
        );
      case MSP.Support.No:
        return (
          <p style={{ margin: 0, color: "#a80000", fontWeight: "bold" }}>
            {this.CamelCaseToSentence(MSP.Support[value])}
          </p>
        );
      case MSP.Support.Yes:
        return (
          <p style={{ margin: 0, color: "#06a800", fontWeight: "bold" }}>
            {this.CamelCaseToSentence(MSP.Support[value])}
          </p>
        );
      case MSP.Support.YesWithCaveats:
        return (
          <p style={{ margin: 0, color: "#EE9900", fontWeight: "bold" }}>
            {this.CamelCaseToSentence(MSP.Support[value])}
          </p>
        );
      default:
        return <></>;
    }
  }

  private FormatRegion(
    constituency?: MSP.Constituency,
    region?: MSP.Region
  ): string {
    if (region) {
      return this.CamelCaseToSentence(MSP.Region[region]);
    } else if (!constituency) {
      return "none";
    } else {
      const newRegion = ConstituencyToRegionMap.Collection.find(
        r => r.const === constituency
      )?.region;
      if (newRegion) {
        return this.CamelCaseToSentence(MSP.Region[newRegion]);
      } else {
        return "none";
      }
    }
  }

  private CamelCaseToSentence(passed: string) {
    const result = passed.replace(/([A-Z])/g, " $1");
    let final = result.charAt(0).toUpperCase() + result.slice(1);
    final = final.replace("And", "and");
    return final;
  }
  private FormatParty(party: MSP.Party) {
    switch (party) {
      case MSP.Party.ScottishConservativeParty:
        return (
          <div key={party.toString()}>
            <img
              className={"con logo"}
              src={Tories}
              alt={"Conservative logo"}
            />
            {this.CamelCaseToSentence(MSP.Party[party])}
          </div>
        );
      case MSP.Party.ScottishGreenParty:
        return (
          <div key={party.toString()}>
            <img
              className={"green logo"}
              src={Greens}
              alt={"Conservative logo"}
            />
            {this.CamelCaseToSentence(MSP.Party[party])}
          </div>
        );
      case MSP.Party.ScottishLiberalDemocrats:
        return (
          <div key={party.toString()}>
            <img
              className={"libdem logo"}
              src={LibDems}
              alt={"Conservative logo"}
            />
            {this.CamelCaseToSentence(MSP.Party[party])}
          </div>
        );
      case MSP.Party.ScottishLabourParty:
        return (
          <div key={party.toString()}>
            <img
              className={"labour logo"}
              src={Labour}
              alt={"Conservative logo"}
            />
            {this.CamelCaseToSentence(MSP.Party[party])}
          </div>
        );
      case MSP.Party.ScottishNationalParty:
        return (
          <div key={party.toString()}>
            <img className={"snp logo"} src={SNP} alt={"Conservative logo"} />
            {this.CamelCaseToSentence(MSP.Party[party])}
          </div>
        );
      case MSP.Party.ReformUkScotland:
        return (
          <div key={party.toString()}>
            <img
              className={"reform logo"}
              src={ReformUK}
              alt={"Conservative logo"}
            />
            {this.CamelCaseToSentence(MSP.Party[party])}
          </div>
        );
      default:
        return (
          <div key={party.toString()}>
            <div className={"logo"} />
            {this.CamelCaseToSentence(MSP.Party[party])}
          </div>
        );
    }
  }

  private FormatName(passed: string, incumbent: boolean, support: MSP.Support) {
    let text = passed.trim();
    if (passed.includes(",")) {
      const names = passed.split(",");
      text = names[1] + " " + names[0];
    }
    text = text.trim();
    if (incumbent) {
      switch (support) {
        case MSP.Support.NoReply:
          return (
            <p
              key={text}
              style={{ margin: 0, color: "#555555", fontWeight: "bold" }}
            >
              {text}
            </p>
          );
        case MSP.Support.No:
          return (
            <p
              key={text}
              style={{ margin: 0, color: "#a80000", fontWeight: "bold" }}
            >
              {text}
            </p>
          );
        case MSP.Support.Yes:
          return (
            <p
              key={text}
              style={{ margin: 0, color: "#06a800", fontWeight: "bold" }}
            >
              {text}
            </p>
          );
        case MSP.Support.YesWithCaveats:
          return (
            <p
              key={text}
              style={{ margin: 0, color: "#EE9900", fontWeight: "bold" }}
            >
              {text}
            </p>
          );
        default:
          return <></>;
      }
    } else {
      switch (support) {
        case MSP.Support.NoReply:
          return (
            <p key={text} style={{ margin: 0, color: "#555555" }}>
              {text}
            </p>
          );
        case MSP.Support.No:
          return (
            <p key={text} style={{ margin: 0, color: "#a80000" }}>
              {text}
            </p>
          );
        case MSP.Support.Yes:
          return (
            <p key={text} style={{ margin: 0, color: "#06a800" }}>
              {text}
            </p>
          );
        case MSP.Support.YesWithCaveats:
          return (
            <p key={text} style={{ margin: 0, color: "#EE9900" }}>
              {text}
            </p>
          );
        default:
          return <></>;
      }
    }
  }

  private ConvertIncumbentName(passed: string): string {
    const names = passed.split(",");
    return (names[1] + " " + names[0]).trim();
  }

  private SortCandidates = (collection: CandidateInfo[]) => {
    collection.sort((n1, n2) => {
      if (
        (n1.name.key ? n1.name.key : "a") > (n2.name.key ? n2.name.key : "a")
      ) {
        return 1;
      }

      if (
        (n1.name.key ? n1.name.key : "a") < (n2.name.key ? n2.name.key : "a")
      ) {
        return -1;
      }

      return 0;
    });
  };

  private SortIncumbents = (collection: IncumbentInfo[]) => {
    collection.sort((n1, n2) => {
      if (
        (n1.name.key ? n1.name.key : "a") > (n2.name.key ? n2.name.key : "a")
      ) {
        return 1;
      }

      if (
        (n1.name.key ? n1.name.key : "a") < (n2.name.key ? n2.name.key : "a")
      ) {
        return -1;
      }

      return 0;
    });
  };

  private SelectAllCandidates = () => {
    this.setState({ SectionSelected: SelectedSection.AllCandidates });
  };

  private SelectIncumbents = () => {
    this.setState({ SectionSelected: SelectedSection.Incumbents });
  };

  private readonly ConstituencyMap = (
    selected: ValueType<IConstituencies, false>,
    action: ActionMeta<IConstituencies>
  ): void => {
    if (selected) {
      const result = new Array<IMSPSupport>();
      const msps = this.GetConstituencyMSPs(selected.value);
      msps.forEach(m =>
        result.push({
          name: this.FormatName(m.Name, false, m.Support),
          party: this.FormatParty(m.Party),
          support: m.Support,
          constituency: m.Constituency,
          statement: m.Statement,
          regionRank: m.RegionRank
        })
      );
      this.setState({
        Constituency: { constituency: selected.value, msps: result }
      });
    }
  };

  private GetConstituencyMSPs(constituency: MSP.Constituency): MSP.MSP[] {
    const region = ConstituencyToRegionMap.Collection.find(
      r => r.const === constituency
    )?.region;
    const constituencies = ConstituencyToRegionMap.Collection.filter(
      r => r.region === region
    );
    const constituencyCandidates = Candidates.MSPs.MSPsCollection.filter(
      m => m.Constituency === constituency
    );
    const regionalCandidates = Candidates.MSPs.MSPsCollection.filter(
      m =>
        m.Region === region ||
        (m.Constituency &&
          constituencies.map(c => c.const).includes(m.Constituency) &&
          m.RegionRank)
    );
    constituencyCandidates.forEach(c => {
      if (!regionalCandidates.map(r => r.Name).includes(c.Name)) {
        regionalCandidates.push(c);
      }
    });
    regionalCandidates.concat(constituencyCandidates);
    const constituencyMSP = MSPs.MSPs.MSPsCollection.find(
      m => m.Constituency === constituency && !m.StandingDown
    );
    const regionalMSPs = MSPs.MSPs.MSPsCollection.filter(
      m => m.Region === region && m.RegionOnly && !m.StandingDown
    );
    if (constituencyMSP) {
      const candidate = regionalCandidates.find(
        m => m.Name.trim() === this.ConvertIncumbentName(constituencyMSP.Name)
      );
      if (!candidate) {
        regionalCandidates.push(constituencyMSP);
      }
    }
    regionalMSPs.forEach(r => {
      const candidate = regionalCandidates.find(
        m => m.Name.trim() === this.ConvertIncumbentName(r.Name)
      );
      if (!candidate) {
        regionalCandidates.push(r);
      } else if (candidate) {
        candidate.Support = r.Support;
        candidate.Statement = r.Statement;
        candidate.Constituency = r.Constituency
          ? r.Constituency
          : candidate.Constituency != null
          ? candidate.Constituency
          : undefined;
      }
    });
    return regionalCandidates
      .sort(m => m.Party)
      .sort(m => (m.RegionRank ? m.RegionRank : 0));
  }
}

export default TablePage;
